.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  height: 80px;
}
  
.navbar-container {
  display: flex;
  align-items: center;
  height: 80px;
  max-width: 1500px;
  justify-content: center;
}
  
.navbar-logo {
  justify-self: start;
  margin-left: 20px;
  font-family: "Roboto";
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  color: #fff;
}